header{
  margin-top:20%;
  text-align: center;
  color: #20253F;
  font-family: 'Quicksand', sans-serif;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.header__wrapper{
  width: 60%;
}
.header__wrapper__social-icons{
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.header__wrapper__social-icons a{
  all:unset;
  font-size: 1.5em;
  -webkit-transition: .2s all;
  -o-transition: .2s all;
  transition: .2s all;
  padding: 5px;
  border-radius: 5px;
  width: 1em;
  height: 1em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.header__wrapper__social-icons a:hover{
  color:white;
  background: #20253F;
}
.header__wrapper__logo{
  width: 50%;
  border-radius: 50%;
}



