.message{
  width: 100%;
  margin: 20px 0 20px 0;
}
.message__text{
  padding: 5px 10px 5px 10px;
    border-radius: 5px;
  background: #eee;
display: -ms-inline-flexbox;
display: inline-flex;
-ms-flex-align: center;
    align-items: center;
-ms-flex-pack: distribute;
    justify-content: space-around;
    min-width: 2.5em;
  color: black;
  font-size: 1.2em;
height: 1.5em;
}
.message__date{
  padding: 5px;
  display: block;
  font-size: 0.9em;
  color: #eee;
}
.message__text__dots, .message__text__dots::before, .message__text__dots::after{
  display: inline-block;
  width: 12px;
  height: 12px;
  background: black;
  border-radius: 50%;

  -webkit-animation: up 1.3s linear infinite;

          animation: up 1.3s linear infinite;
}
.message__text__dots{
  position: relative;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.message__text__dots::before{
  content: '';
  position: absolute;
  left: -18px;
  top: 0;
  	-webkit-animation-delay: 2s;
  	        animation-delay: 2s;
}
.message__text__dots::after{
  content: '';
  position: absolute;
  right: -18px;
  top: 0;
  -webkit-animation-delay: 0;
          animation-delay: 0;
}
@-webkit-keyframes up {
  0%,60%,100%{
     -webkit-transform:translateY(0px);
             transform:translateY(0px);
  }
  30%{
     -webkit-transform:translateY(-0.5em);
             transform:translateY(-0.5em);
  }
}
@keyframes up {
  0%,60%,100%{
     -webkit-transform:translateY(0px);
             transform:translateY(0px);
  }
  30%{
     -webkit-transform:translateY(-0.5em);
             transform:translateY(-0.5em);
  }
}



