.route-wrapper--home{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack: center;
      justify-content: center;
  position: relative;
}
.route-wrapper--home__arrow{
  position: absolute;
  bottom: 10%;
  left: 50%;
  color: white;
  font-size: 2em;
  opacity: 0;
  cursor: pointer;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.route-wrapper--home__container{
  width: 100%;
  padding-left: 10%;
  height: 5em;
}
.route-wrapper--home__container__header-container{
  width: 100%;
height: 5em;
}
.route-wrapper--home__header-wrapper{
  position: relative;
  width: 100%;
}
.route-wrapper--home__header-wrapper__header{
  position: absolute;
  font-size: 5em;
  font-family: 'Quicksand';
  z-index: 1;
  margin: 0;
}
.route-wrapper--home__header-wrapper__header.animated{
 -webkit-transform: translate3d(-3000px, 0, 0);
         transform: translate3d(-3000px, 0, 0);
}
.route-wrapper--home__header-wrapper__header--shadow{
  position: absolute;
  font-size: 5em;
  font-family: 'Quicksand';
    margin: 0;
  z-index: 0;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  background: -webkit-repeating-linear-gradient(
  45deg,
  #e74c3c,
  #e74c3c 10px,
  #c52a1a 10px,
  #c52a1a  20px
);
  background: -o-repeating-linear-gradient(
  45deg,
  #e74c3c,
  #e74c3c 10px,
  #c52a1a 10px,
  #c52a1a  20px
);
  background: repeating-linear-gradient(
  45deg,
  #e74c3c,
  #e74c3c 10px,
  #c52a1a 10px,
  #c52a1a  20px
);
   -webkit-background-clip: text;
  color: transparent;
  background-size: 56px 56px;
   background-position: 0 0;
   -webkit-animation: slide 30s infinite linear forwards;
   animation: slide 30s infinite linear forwards;
}
@keyframes slide {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 100% 0;
  }
}
@-webkit-keyframes slide {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 100% 0;
  }
}



