
/* The browser window */
.browser{
  width: 100%;
    -webkit-transition: .2s all;
    -o-transition: .2s all;
    transition: .2s all;
}
.browser:hover input{
  zoom:2;
}
/* Container for columns and the top "toolbar" */
.browser__toolbar {
    background: #f1f1f1;
    height: 3em;
}
.browser__toolbar__inner{
  margin: 0 10px 10px 10px;
      display: -ms-flexbox;
      display: flex;
}
[class^='browser__toolbar__col']{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.browser__toolbar__col--left {
    width: 15%;
    margin-left: 10px;
}

.browser__toolbar__col--right {
    width: 10%;
    -ms-flex-pack: center;
        justify-content: center;
    color: #d1d1d1;
}

.browser__toolbar__col--middle {
    width: 75%;
}

/* Three dots */
.browser__toolbar__button {
    margin:3px;
    height: 12px;
    width: 12px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
}

.browser__toolbar__input {
    width: 100%;
    border-radius: 3px;
    border: none;
    background-color: white;
    color: #666;
    padding: 5px;
    height: 1em;
}


/* Page content */
.browser__content {
    padding: 10px;
}



