.route-wrapper--more-about-me{
  color: white;
  font-family: 'Ubuntu Mono';
  height: 100vh !important;
}
.route-wrapper--more-about-me__wrapper__line--indicator--user{
  color: #54DC59;
}
.route-wrapper--more-about-me__wrapper__line--indicator--colon-dollar{
  color: #7799CA;
}
.route-wrapper--more-about-me__wrapper__line--indicator--tilde{
  color: white;
}
.route-wrapper--more-about-me__wrapper__line--indicator--command{
  color: white;
  margin-left: .5em;
}
.route-wrapper--more-about-me__wrapper__line--indicator--command .Typist{
  display: inline-block;
}
.route-wrapper--more-about-me__wrapper{
  position: relative;
  width: 100%;
  height: 100%;
}
.route-wrapper--more-about-me__wrapper__help{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  color: black;
  background: rgba(255,255,255,0.9);
}



