.route-wrapper--about{
  color: white;
  font-style: 'Quicksand';
  background: url('http://www.pkin.pl/frontend/video/main.jpg');
}
.route-wrapper--about__wrapper{
  width: 100%;
  height: 100vh;
  background: rgba(32,37,63, 0.95);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack: center;
      justify-content: center;
  font-family: 'Titillium Web';
  position: relative;
}
.route-wrapper--about__portfolio--container{
  position: absolute;
  top:10px;
  right: 10px;
  z-index: 2;
}
.route-wrapper--about__wrapper__container{
  width: 80%;
}
.route-wrapper--about__wrapper__container h2{


}
.route-wrapper--about__wrapper__container__important{
  color: #FF2449;
  font-size: 1.2em;
}
.route-wrapper--about__wrapper__container_apps-container{
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 5% 0 5% 0;
}
.route-wrapper--about__wrapper__container__i{
  width: 2em;
}
.route-wrapper--about__wrapper__container_apps-container__app{
  width: 25%;
  text-align: center;
}
.route-wrapper--about__wrapper__container_apps-container__app:hover i{
color: rgba(255,255,255,0.8);
}
.route-wrapper--about__wrapper__container_apps-container__app i{
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  font-size: 4em;
}
.route-wrapper--about__wrapper__container__buttons{
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  position: relative;
}
.route-wrapper--about__wrapper__container__buttons a{
  min-width: 20%;
}
.route-wrapper--about__wrapper__container__buttons__btn{
  color: white;
  text-decoration: none;
  font-size: 1.2em;
  padding: 5px;
  border: 1px solid white;
  border-radius: 5px;
  /* display: flex;
  align-items: center;
  justify-content: space-between; */
  width: 100%;
}
.route-wrapper--about__wrapper__container__buttons__btn__i{
font-size: 1.2em;
width: 1.5em;
}



